
/* eslint-disable */
let me
let vm
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import {goodsApi} from '../../service/main.js'
export default {
name: 'goods-detail',
data () {
return {
show: false,
curOrderFilter: '',
imgs: [],
list: ['商品详情', '规格', '评论'],
params: {
type: 0,
pagerSize: 10,
pageNo: 1,
goodsType: 'XXX',
goodsCategory: '',
brandId: '',
filter: '',
},
isPosting: false,
onFetching: false,
curIndex: 0,
demo: 0,
getBarWidth: function (index) {
return (index + 1) * 22 + 'px'
}
}
},
components: {Tab, TabItem, Swiper, SwiperItem},
beforeMount() {
me = window.me
},
mounted () {
vm = this
vm.id = vm.$route.query.id
vm.getDetail(vm.mySwiper)




},
computed: {},
watch: {
'$route' (to, from) {
vm.getDetail()
}
},
methods: {

setPageStatus (data) {
this.$emit('listenPage', data)
},
mySwiper () {
return new Swiper('.swiper-detail .swiper-container', {
initialSlide: 0,
direction: 'horizontal',
autoplay: 2000,
preloadImages: true,
autoplayDisableOnInteraction: false,
observer: true,
observeParents: true,

pagination: '.swiper-pagination',
paginationClickable: true,



grabCursor: true,





})
},
toAppraise (id) {
this.$router.push({path: '/appraise' + (param ? '/' + param : '')})
},
addToCart (id) {
this.$router.push({path: '/appraise' + (param ? '/' + param : '')})
},
refresh (done) {
console.log('下拉加载')
setTimeout(function () {
vm.getOrders()
vm.$refs.orderScroller.finishPullToRefresh()
}, 1200)
},
infinite (done) {
console.log('无限滚动')
setTimeout(function () {
vm.getOrders(true)
vm.$refs.orderScroller.finishInfinite(true)
}, 1000)
},
onItemClick2 (type) {
if (type === 'undefined') {
vm.params.type = ''
} else {
vm.params.type = type
}
vm.getOrders()
},
filterTicket (type, isMine) {
vm.curTicketFilter = type
vm.getOrders()
},
getDetail (cb) {
vm.params.type = vm.$route.params.id
if (vm.onFetching) return false
vm.processing()
vm.onFetching = true
vm.loadData(goodsApi.detail, vm.params, 'POST', function (res) {

vm.imgs = res.data.itemList
console.log(vm.imgs, '商品图片数据')
cb ? cb() : null
vm.onFetching = false
vm.processing(0, 1)
}, function () {
vm.onFetching = false
vm.processing(0, 1)
})
},
delOrder (id) {
if (vm.isPosting) return false
vm.confirm('确认删除？', '订单删除后不可恢复！', function () {
vm.isPosting = true
vm.loadData(orderApi.delOrder + '?id=' + id, vm.params, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {
})
},
onItemClick (index) {
console.log('on item click:', index)
},
addTab () {
if (this.list.length < 5) {
this.list = list().slice(0, this.list.length + 1)
}
},
removeTab () {
if (this.list.length > 1) {
this.list = list().slice(0, this.list.length - 1)
}
},
next () {
if (this.curIndex === this.list.length - 1) {
this.curIndex = 0
} else {
++this.curIndex
}
},
prev () {
if (this.curIndex === 0) {
this.curIndex = this.list.length - 1
} else {
--this.curIndex
}
}
}
}



