
/* eslint-disable no-unused-vars */
let me
let vm
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {userApi} from '../../service/main.js'

export default {
name: 'login-con',
data() {
return {
isPosting: false,
btnText: '发送验证码',
btnStatus: false,
sellerId: null,
phone: null,
code: null,
passwd: null,
lastPasswd: null
}
},
components: {Group, Cell, XInput, XButton},
beforeMount() {
me = window.me
},
mounted() {
vm = this
me.attachClick()
vm.sellerId = vm.$route.query.id
},
methods: {
getCode() {
if (vm.isPosting) return false
if (!vm.phone) {
vm.toast('请填写手机号 ！', 'warn')
return false
}
if (!vm.phone.match(/^(13|15|18|17)\d{9}$/)) {
vm.toast('请填写正确的手机号 ！', 'warn')
return false
}
vm.isPosting = true
vm.loadData(userApi.code, {phone: vm.phone}, 'POST', function (res) {
vm.toast('已发送，请注意查收！')
vm.btnText = '60s后再次获取'
vm.btnStatus = true
setTimeout(function(){
vm.btnText = '发送验证码'
vm.btnStatus = false
},6000)
vm.isPosting = false
}, function () {
vm.isPosting = false
})
},
update() {
if (vm.isPosting) return false
if (!vm.phone) {
vm.toast('请填写手机号 ！', 'warn')
return false
}
if (!vm.phone.match(/^(13|15|18|17)\d{9}$/)) {
vm.toast('请填写正确的手机号 ！', 'warn')
return false
}
if (!vm.code) {
vm.toast('请填写验证码 ！', 'warn')
return false
}
if (vm.sellerId) {
if (!vm.lastPasswd) {
vm.toast('请填写旧密码 ！', 'warn')
return false
}
}
if (!vm.passwd) {
vm.toast('请填写密码 ！', 'warn')
return false
}
vm.isPosting = true
vm.loadData(userApi.login, {userId: vm.userId, nickName: vm.nickName}, 'POST', function (res) {
console.log(res, '修改用户信息')
vm.toast('密码已更新')
vm.$router.back()
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}
}
}



