
/* eslint-disable no-unused-vars */
let me
let vm
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {goodsApi} from '../../service/main.js'

export default {
name: 'goods',
data() {
return {
curTicketFilter: '',
goods: [],
params: {
type: 0,
pagerSize: 10,
pageNo: 1,
saleStatus: 0,
category: ''
},
onFetching: false,
isPosting: false
}
},
components: {Tab, TabItem},
beforeMount() {
me = window.me
},
mounted() {
vm = this
vm.getGoods()
vm.$nextTick(() => {
vm.$refs.goodsScroller.finishInfinite(true)
vm.$refs.goodsScroller.resize()
})
},
/* computed: {
'params.type' () {
return this.$route.params.type
}
}, */
watch: {
'$route'(to, from) {
vm.getGoods()
}
},
methods: {

setPageStatus(data) {
this.$emit('listenPage', data)
},
buy(id) {
vm.$router.push({path: '/detail/' + id})
},
getGoods(isLoadMore, status) {
vm.params.type = this.$route.params.type || 0
if (vm.onFetching) return false
vm.onFetching = true
vm.loadData(goodsApi.list, vm.params, 'POST', function (res) {
if (!isLoadMore) {
vm.goods = res.data.pager.itemList
} else {
vm.goods.push(res.data.pager.itemList)
}
console.log(vm.goods, '商品数据')
vm.onFetching = false
}, function () {
vm.onFetching = false
})
},
refresh(done) {
console.log('下拉加载')
setTimeout(function () {
vm.getGoods()
vm.$refs.goodsScroller.finishPullToRefresh()
}, 1200)
},
infinite(done) {
console.log('无限滚动')
setTimeout(function () {
vm.getGoods(true)
vm.$refs.goodsScroller.finishInfinite(true)
}, 1000)
},
filterStatus(status) {
vm.params.saleStatus = status || 0
vm.getGoods(false, vm.filterStatus)
},
delGoods(id) {
if (vm.isPosting) return false
vm.confirm('确认删除？', '商品删除后不可恢复！', function () {
vm.isPosting = true
vm.loadData(goodsApi.delOrder + '?id=' + id, vm.params, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {
})
},
setState(id, status) {
if (vm.isPosting) return false
vm.isPosting = true
vm.loadData(goodsApi.setSaleStatus, {id: id, saleStatus: status}, 'POST', function (res) {
vm.toast(status === 1 ? '上架成功' : '已下架')
vm.getGoods()
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {
},
editGoods(data) {
vm.$router.push({name: 'edit_goods', query: {linedata: encodeURIComponent(JSON.stringify(data))}})
},
pushOrder(id) {
if (vm.isPosting) return false
vm.confirm('确认催单？', '请不要频繁催单！', function () {
vm.isPosting = true
vm.loadData(goodsApi.cancelOrder + '?id=' + id, vm.params, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {

})
},
}
}



