
/* eslint-disable no-unused-vars */
let me
let vm
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import XInput from 'vux/src/components/x-input/index.vue'

import {userApi} from '../../service/main.js'
export default {
name: 'address-edit',
data () {
return {
onFetching: false,
isPosting: false,
addressData: ChinaAddressV3Data,
param: {
userId: null,
id: null,
name: '',
phone: null,
area: null,
address: null,
detailAddress: '',
postCode: null
}
}
},
components: {Group, Cell, XAddress, XInput},
beforeMount () {
me = window.me
},
mounted () {
vm = this

vm.param.id = vm.$route.query.id
vm.param.userId = vm.$store.state.global.userId
console.log(vm.param)
},
methods: {
logHide (str) {
console.log('on-hide', str)
},
changeArea (ids, names) {
console.log(ids, names)
vm.param.area = names.join('')
},
updateAddress () {
if (vm.isPosting) return false
console.log(vm.param)
if (!vm.param.name || !vm.param.phone || !vm.param.area || !vm.param.detailAddress) {
vm.toast('请先填写完整！', 3)
return false
}
vm.param.address = vm.param.area + vm.param.detailAddress
vm.$store.commit('updateAddress', {data: vm.param, type: 'push'})
vm.$router.back()

/* vm.isPosting = true
