
/* eslint-disable */
let me
let vm
import Swiper from '../components/Swiper'
import Geo from '../components/Geo'
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import {homeApi, orderApi, goodsApi} from '../service/main.js'
import {mapState, mapMutations} from 'vuex'

export default {
name: 'home',
data() {
return {
geo: null,
isMilk: false,
location: '',
type: 0,
orders: [],
scrollTop: 0,
isPosting: false,
params: {
userType: 1,
goodsType: 'water',
pagerSize: 10,
pageNo: 1
},
noMore: false,
pulldownConfig: {
content: '下拉刷新',
height: 60,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '松开以加载',
loadingContent: '加载中…',
clsPrefix: 'xs-plugin-pulldown-'
},
pullupConfig: {
content: '上滑加载更多',
height: 40,
pullUpHeight: 60,
autoRefresh: false,
downContent: '上滑加载',
upContent: '上滑加载',
loadingContent: '加载中…',
clsPrefix: 'xs-plugin-pullup-'
}
}
},
components: {
Geo,
Group,
GroupTitle,
Grid,
GridItem,
Marquee,
MarqueeItem,
Countup,
Tab,
TabItem,
XSwitch,
Sticky
},
beforeMount() {
me = window.me
},
mounted() {
vm = this
me.attachClick()
vm.getOrders()
vm.$nextTick(function () {
vm.$nextTick(function () {
vm.$refs.orderScroller.finishInfinite(true)
vm.$refs.orderScroller.resize()
})
})
},
computed: {},
watch: {
'$route'(to, from) {
vm.getOrders()
},
isMilk() {
vm.params.goodsType = vm.isMilk ? 'milk' : 'water'
vm.getOrders()
}
},
methods: {

getMap(data) {
console.log(data, 'home geo info')
this.geo = data
},

setPageStatus(data) {
this.$emit('listenPage', data)
},
scrollHandler() {

setTimeout(function () {
let scrollTop = vm.$refs.home.scrollTop
let target = vm.$refs.orderTab
let list = vm.$refs.goodsList
if (vm.showFilterCon) {
vm.hideFilter()
}
if (scrollTop >= vm.orderOffset) {
target.classList.add('fixed')
list.classList.add('fixed')
} else {
target.classList.remove('fixed')
list.classList.remove('fixed')
}
}, 300)
},
/* 页面数据 */
changeType() {

},
/* 上下拉刷新 */
onPullDown() {
if (vm.isPosting) return false

setTimeout(function () {
vm.getGoods()
vm.$nextTick(function () {
vm.$refs.myScroll.reset({top: 0})
vm.$refs.myScroll.donePullup()
vm.$refs.myScroll.donePulldown()
})
}, 1500)
},
onPullUp() {
if (vm.isPosting) {

return false
} else {
/* if(vm.$refs.myScroll.top<500){
return
} */

setTimeout(function () {
vm.getGoods(true)
vm.$nextTick(function () {
vm.$refs.myScroll.reset({bottom: 0})
vm.$refs.myScroll.donePullup()
vm.$refs.myScroll.donePulldown()
})
}, 200)
}
},
onScroll(pos) {
this.scrollTop = pos.top
vm.factive = ''
vm.showFilterCon ? vm.showFilterCon = false : null
},
refresh(done) {
console.log('下拉加载')
setTimeout(function () {
vm.getOrders()
vm.$refs.orderScroller.finishPullToRefresh()
}, 1200)
},
infinite(done) {
console.log('无限滚动')
setTimeout(function () {
vm.getOrders(true)
vm.$refs.orderScroller.finishInfinite(true)
}, 1000)
},
getOrders(isLoadMore) {
if (vm.isPosting) return false
vm.processing()
vm.isPosting = true
vm.loadData(orderApi.list, vm.params, 'POST', function (res) {
vm.isPosting = false
vm.processing(0, 1)
var resD = res.data.pager
if (resD.itemList.length) {
for (var i = 0; i < resD.itemList.length; i++) {
var cur = resD.itemList[i]
switch (cur.status) {
case -1:
cur.statusName = '已取消'
break
case 1:
cur.statusName = '待支付'
break
case 2:
cur.statusName = '待派送'
break
case 3:
cur.statusName = '派送中'
break
/*case 3:
cur.statusName = '待评价'
break*/
case 4:
cur.statusName = '已完成'
break
}
}
}
if (!isLoadMore) {
if (resD.totalCount < vm.params.pageSize) {
vm.noMore = true
} else {
vm.noMore = false
}
vm.orders = resD.itemList
} else {
resD.itemList.length ? vm.orders.concat(resD.itemList) : vm.noMore = true
}
console.log(vm.orders, '订单数据')
}, function () {
vm.isPosting = false
vm.processing(0, 1)
})
},
delOrder(id) {
if (vm.isPosting) return false
vm.confirm('确认删除？', '订单删除后不可恢复！', function () {
vm.isPosting = true
vm.loadData(orderApi.del, {id: id}, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {
})
},
cancelOrder(id) {
if (vm.isPosting) return false
vm.confirm('确认取消？', '订单取消后不可恢复！', function () {
vm.isPosting = true
vm.loadData(orderApi.cancel, {id: id}, 'POST', function (res) {
vm.isPosting = false
vm.toast('已取消')
}, function () {
vm.toast('取消失败')
vm.isPosting = false
})
}, function () {

})
},
pushPay(id) {
if (vm.isPosting) return false
vm.isPosting = true
vm.loadData(orderApi.push, {id: id}, 'POST', function (res) {
vm.toast('提醒成功')
vm.isPosting = false
}, function () {
vm.isPosting = false
})
},
dispatchOrder(id) {
if (vm.isPosting) return false
vm.confirm('确认派送？', '', function () {
vm.isPosting = true
vm.loadData(orderApi.push, {id: id}, 'POST', function (res) {
vm.toast('已确认')
vm.isPosting = false
}, function () {
vm.isPosting = false
})
})
},
onItemClick(status) {
status ? vm.params.status = status : delete vm.params.status
vm.getOrders()
}
}
}



