
/* eslint-disable */

let vm
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
name: 'auth',
data () {
return {
webUrl: 'http://www.baidu.com'
}
},
created () {

if(!me.locals.get('ynWxUser')){
if(me.isWeixin){

window.location.href = this.webUrl + '/Wap/User/getOpenid'
}else{

}
}else{

this.login()
}
},
components: {Tabbar, TabbarItem},
beforeMount () {

},
mounted () {

vm = this
},
computed: {
},
methods: {

getPageStatus (data) {
vm.curSelected = data
},
login () {
let url = this.webUrl + '/Wap/User/info'

this.$http.get(url).then(response => {
response = response.body
console.log(response)
if(response){

this.$store.commit('getUser', response)
setTimeout(() => {
this.goBeforeLoginUrl() // 页面恢复(进入用户一开始请求的页面)
}, 2000)
}else{
this.alert('服务器tm撸猫去了 :(', 'wrong')
if(me.locals.get('ynWxUser')){

window.location.href = this.webUrl + '/Wap/User/getOpenid'
}
}
})
},
goBeforeLoginUrl () {
let url = me.locals.get('beforeLoginUrl')
if(!url || url.indexOf('/author') !== -1){
this.$router.push({path: '/home'})
}else{
this.$router.push({path: url})
me.locals.set('beforeLoginUrl', '')
}
}
},
watch: {

}
}



