
/* eslint-disable no-unused-vars */
let me
let vm
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import {userApi, commonApi} from '../../service/main.js'

export default {
name: 'coupon-edit',
data() {
return {
formatValueFunction(val) {
return val.replace(/-/g, '$')
},
isPosting: false,
couponId: null,
coupons: null,
types: [{key: 1, value: '优惠券', name: '优惠券'}, {key: 2, value: '满减', name: '满减'}],
params: {
id: null,
name: '',
type: 1,
discount: null,
beginTime: '',
endTime: '',
discountNote: ''
},
tmpType: []
}
},
components: {
Group,
XInput,
Datetime,
PopupPicker,
XTextarea
},
beforeMount() {
me = window.me
},
mounted() {
vm = this

vm.params.sellerId = vm.$store.state.global.sellerId
vm.params.id = vm.$route.query.id
vm.params.id ? vm.getData() : null
},
methods: {
setToday() {
let now = new Date()
let cmonth = now.getMonth() + 1
let day = now.getDate()
if (cmonth < 10) cmonth = '0' + cmonth
if (day < 10) day = '0' + day
vm.params.beginTime = now.getFullYear() + '-' + cmonth + '-' + day
},
switchData(data, value, target,) {
let tmp
if (typeof value === 'number') {
tmp = []
for (let i = 0; i < data.length; i++) {
if (value === data[i].key) {
tmp.push(data[i].name)
}
}
vm[target] = tmp
} else {
let tt = value.join('')
for (let i = 0; i < data.length; i++) {
if (tt === data[i].name) {
tmp = data[i].key
}
}
vm.params[target] = tmp
}
},
getData() {
if (vm.isPosting) return false
vm.isPosting = true
vm.loadData(userApi.get, {couponId: id}, 'POST', function (res) {
vm.isPosting = false
if (res) {
let resD = res.data.itemList
/*此处转换一些字段类型*/
vm.switchData(vm.types, vm.params.type, 'tmpType')
vm.coupons = resD
console.log(vm.coupons)
}
}, function () {
vm.isPosting = false
})
},
validate() {
if (!vm.tmpType.length) {
vm.toast('请选择优惠分类！')
return false
}
if (!vm.params.name) {
vm.toast('请填写优惠名称！')
return false
}
if (!vm.params.discount) {
vm.toast('请填写抵扣金额！')
return false
}
if (!vm.params.beginTime) {
vm.toast('请选择开始时间！')
return false
}
if (!vm.params.endTime) {
vm.toast('请选择过期时间！')
return false
}
return true
},
update() {
if (vm.isPosting || !vm.validate()) return false
/*此处转换一些字段类型*/
let curApi
vm.switchData(vm.types, vm.tmpType, 'type')
if (vm.couponId) {
curApi = userApi.updateCoupon
vm.params.id = vm.couponId
} else {
delete vm.params.id
curApi = userApi.addCoupon
}
console.log('最后选择的数据：', vm.params)
vm.isPosting = true
vm.processing()
vm.loadData(curApi, vm.params, 'POST', function (res) {
vm.isPosting = false
vm.processing(0, 1)
vm.$router.back()
}, function () {
vm.isPosting = false
vm.processing(0, 1)
})
},
onChange(val) {
console.log('change', val)
},
changeType(val) {
vm.switchData(vm.types, vm.tmpType, 'type')
console.log(val, vm.params.type)
}
}
}



