
/* eslint-disable no-unused-vars */
let me
let vm
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {ticketApi} from '../service/main.js'
export default {
name: 'ticket',
data () {
return {
curTicketFilter: '',
tickets: [],
params: {
type: 0,
pagerSize: 10,
pageNo: 1,
goodsType: 'XXX',
goodsCategory: '',
brandId: '',
filter: ''
},
onFetching: false,
isPosting: false
}
},
components: {Tab, TabItem},
beforeMount () {
me = window.me
},
mounted () {
vm = this
vm.getTickets()
vm.$nextTick(() => {
vm.$refs.ticketScroller.finishInfinite(true)
vm.$refs.ticketScroller.resize()
})
},
/* computed: {
'params.type' () {
return this.$route.params.type
}
}, */
watch: {
'$route' (to, from) {
vm.getTickets()
}
},
methods: {

setPageStatus (data) {
this.$emit('listenPage', data)
},
buy (id) {
vm.$router.push({path: '/detail/' + id})
},
refresh (done) {
console.log('下拉加载')
setTimeout(function () {
vm.getTickets()
vm.$refs.ticketScroller.finishPullToRefresh()
}, 1200)
},
infinite (done) {
console.log('无限滚动')
setTimeout(function () {
vm.getTickets(true)
vm.$refs.ticketScroller.finishInfinite(true)
}, 1000)
},
onItemClick (type) {
if (type) {
vm.params.type = 2
} else {
vm.params.type = 0
this.$router.push({path: '/ticket'})
}
vm.getTickets()
},
filterTicket (type, isMine) {
vm.curTicketFilter = type
vm.getTickets()
},
getTickets (isLoadMore) {
vm.params.type = this.$route.params.type || 0
if (vm.onFetching) return false

vm.onFetching = true
vm.loadData(ticketApi.tickets, vm.params, 'POST', function (res) {
if (!isLoadMore) {
vm.tickets = res.data.itemList
} else {
vm.tickets.push(res.data.itemList, '水票数据')
}
console.log(vm.tickets)
vm.onFetching = false
}, function () {
vm.onFetching = false
})
}
}
}



