
/* eslint-disable */
let me
let vm
import Search from 'vux/src/components/search/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'

import {clientApi} from '../../service/main.js'

export default {
name: 'clients-con',
data() {
return {
sellerId: null,
value: '',
results: [],
clients: [],
isPosting: false,
onFetching: false
}
},
components: {Search, Swipeout, SwipeoutItem, SwipeoutButton, XProgress},
beforeMount() {
me = window.me
},
mounted() {
vm = this
vm.sellerId = vm.$store.state.global.sellerId
vm.getClients()
vm.$nextTick(() => {
vm.$refs.clientScroller.finishInfinite(true)
vm.$refs.clientScroller.resize()
})
},
computed: {},
/*watch: {
'$route'(to, from) {
}
},*/
methods: {
onButtonClick(type) {
alert('on button click ' + type)
},

setPageStatus(data) {
this.$emit('listenPage', data)
},
toAppraise(id) {
this.$router.push({path: '/appraise' + (param ? '/' + param : '')})
},
refresh(done) {
console.log('下拉加载')
setTimeout(function () {
vm.getClients()
vm.$refs.clientScroller.finishPullToRefresh()
}, 1200)
},
infinite(done) {
console.log('无限滚动')
setTimeout(function () {
vm.getClients(true)
vm.$refs.clientScroller.finishInfinite(true)
}, 1000)
},
getClients(isLoadMore) {
if (vm.onFetching) return false
vm.processing()
vm.onFetching = true
vm.loadData(clientApi.list, {id: vm.sellerId}, 'POST', function (res) {
var resD = res.data.itemList
if (!isLoadMore) {
vm.clients = resD
} else {
vm.clients.push(resD)
}
console.log(vm.clients, '客户数据')
vm.onFetching = false
vm.processing(0, 1)
}, function () {
vm.onFetching = false
vm.processing(0, 1)
})
},
delOrder(id) {
if (vm.isPosting) return false
vm.confirm('确认删除？', '订单删除后不可恢复！', function () {
vm.isPosting = true
vm.loadData(orderApi.delOrder + '?id=' + id, vm.params, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {
})
},
resultClick(item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult(val) {
if (val) {
this.results = []
vm.getClients(false, val)
for (let i = 0; i < vm.clients.length; i++) {
this.results.push({title: vm.clients[i].productName})
}
}
},
onSubmit() {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
vm.getClients()
},
onFocus() {
console.log('on focus')
},
onCancel() {
console.log('on cancel')
}
}
}



