
/* eslint-disable no-unused-vars */
let me
let vm
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {userApi} from '../../service/main.js'
export default {
name: 'my-address',
data () {
return {
onFetching: false,
isPosting: false,
userId: null,
list: null
}
},
components: {Grid, GridItem, Group, Cell},
beforeMount () {
me = window.me
},
mounted () {
vm = this
vm.userId = vm.$route.query.userId

this.$nextTick(function () {
vm.getAddress()
})
},
computed: {},
watch: {
'$route' (to, from) {
vm.getAddress()
}
},
methods: {
getAddress () {
vm.list = vm.$store.state.global.address
/* if (vm.isPosting) return false
vm.isPosting = true
vm.processing()
vm.loadData(userApi.getAddress, {userId: vm.userId}, 'POST', function (res) {
vm.list = res.data.itemList
vm.$store.state.global.address = vm.list
console.log(vm.list, '地址数据')
vm.isPosting = false
vm.processing(0, 1)
}, function () {
vm.isPosting = false
vm.processing(0, 1)
}) */
},
setDefault (id) {
if (vm.isPosting) return false
for (let i = 0; i < vm.list.length; i++) {
vm.list[i].isCurrent = false
id === vm.list[i].id ? vm.list[i].isCurrent = true : null
}
/* vm.isPosting = true
vm.processing()
vm.loadData(userApi.updateAddress, {id: id}, 'POST', function (res) {
console.log(res.data)
vm.isPosting = false
vm.processing(0, 1)
}, function () {
vm.isPosting = false
vm.processing(0, 1)
}) */
},
addAddress () {
if (vm.isPosting) return false
vm.isPosting = true
vm.processing()
vm.loadData(userApi.addAddress, vm.params, 'POST', function (res) {
console.log(res.data)
vm.isPosting = false
vm.processing(0, 1)
}, function () {
vm.isPosting = false
vm.processing(0, 1)
})
},
updateAddress (id) {
},
delAddress (id) {
if (vm.isPosting) return false
vm.confirm('确认删除？', '删除后只能重新添加了！', function () {
for (let i = 0; i < vm.list.length; i++) {
if (id === vm.list[i].id) {
vm.list.splice(i, 1)
vm.$store.commit('updateAddress', {data: vm.list})
}
}
/* vm.isPosting = true
vm.loadData(userApi.delOrder, {id: id}, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
}) */
}, function () {

})
}
}
}



