
/* eslint-disable no-unused-vars */
let me
let vm
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import {userApi} from '../../service/main.js'

export default {
name: 'login-con',
data() {
return {
isPosting: false,
params: {
phone: null,
passwd: null
}
}
},
components: {Group, Cell, XInput},
beforeMount() {
me = window.me
},
mounted() {
vm = this
me.attachClick()
vm.params.phone = vm.$route.query.phone || null
vm.params.passwd = vm.$route.query.psw || null

},
methods: {
login() {
if (vm.isPosting) return false
if (!vm.params.phone) {
vm.toast('请填写手机号 ！', 'warn')
return false
}
if (!vm.params.phone.match(/^(13|15|18|17)\d{9}$/)) {
vm.toast('请填写正确的手机号 ！', 'warn')
return false
}
if (!vm.params.passwd) {
vm.toast('请填写密码 ！', 'warn')
return false
}
vm.isPosting = true
vm.processing()
vm.loadData(userApi.login, vm.params, 'POST', function (res) {
console.log(res, '修改用户信息')
vm.processing(0, 1)
vm.toast('登录成功 ！')

vm.jump('home')
vm.isPosting = false
}, function () {
vm.isPosting = false
vm.processing(0, 1)
})
}
}
}



