
/* eslint-disable no-unused-vars */
let me
let vm
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'

import {commonApi,userApi} from '../../service/main.js'
import imgUploader from '../../components/ImgUploader.vue'
import Amap from '../../components/Amap.vue'

export default {
name: 'seller-edit',
data () {
return {
fileApi: commonApi.uploadImg,
showMap: false,
isPosting: false,
addressData: ChinaAddressV3Data,
seller:{},
sellerId: null,
storeName: '',
authInfo: '',
description: '',
topic: '',
area: '',
detailAddress: '',
params:{
storeImg: '',
lon:'',
lat:'',
address:''
}
}
},
components: {Group, Cell, XInput, XTextarea, XAddress, ChinaAddressV3Data, imgUploader, Amap},
beforeMount () {
me = window.me
},
mounted () {
vm = this

vm.sellerId = vm.$route.params.id
console.log(vm.sellerId)
vm.getSeller()
},
methods: {
getMap(data) {
vm.showMap = false;
console.log(data, 'home amap info')
if(data){
vm.params.lon = data.lng
vm.params.lat = data.lat
vm.detailAddress = data.name
}
},
choosePoint() {
vm.showMap = true;
},
onHide() {

},
getImgUrl(data) {
if (me.isArray(data)) {
vm.params.storeImg = data.join(',')
} else {
vm.params.storeImg = ''
}
},
validate() {
if (!vm.storeName) {
vm.toast('请填写店铺名！')
return false
}
if (!vm.description) {
vm.toast('请填写店铺简介！')
return false
}
if (!vm.detailAddress) {
vm.toast('请填写店铺地址！')
return false
}
},
updateStore() {
console.log({
id: vm.sellerId,
name: vm.storeName,
avtar: vm.storeImg,
authInfo: vm.authInfo,
description: vm.description,
area: vm.area,
detailAddress: vm.detailAddress
})
if (vm.isPosting || !vm.validate()) return false
vm.isPosting = true
vm.processing()
vm.loadData(userApi.set, {
id: vm.sellerId,
name: vm.storeName,
avtar: vm.storeImg,
authInfo: vm.authInfo,
description: vm.description,
area: vm.area,
detailAddress: vm.detailAddress
}, 'POST', function (res) {
vm.isPosting = false
vm.processing(0, 1)
vm.$router.back()
}, function () {
vm.isPosting = false
vm.processing(0, 1)
})
},
getSeller() {
vm.isPosting = true
vm.processing()
vm.loadData(userApi.view, {}, 'POST', function (res) {
vm.isPosting = false
vm.processing(0, 1)
vm.seller=res.data
}, function () {
vm.isPosting = false
vm.processing(0, 1)
})
},
changeArea(ids, names) {
console.log(ids, names)

},
}
}



