
/* eslint-disable no-unused-vars */
let me
let vm
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
name: 'my',
data() {
return {
sellerName: '水一波旗舰店',
sellerId: null,
count: 0
}
},
components: {Grid, GridItem, Group, Cell},
beforeMount() {
me = window.me
},
mounted() {

vm = this
vm.sellerId = vm.$store.state.global.sellerId
},
/* watch: {
'$route'(to, from) {
this.count = this.$store.state.cart.count
}
},*/
computed: {},
methods: {

setPageStatus(data) {
this.$emit('listenPage', data)
},
logout() {
vm.confirm('退出登录？', '', function () {
vm.$store.commit('logout')
vm.$router.push({name: 'login'})
})
},
modPassword() {
this.$router.push({name: 'password', query: {id: vm.sellerId}})
},
}
}



