
/* eslint-disable */


let vm
import Home from './pages/Home'
import {commonApi} from './service/main.js'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import {mapState, mapActions} from 'vuex'

export default {
name: 'app',
data() {
return {
transitionName: 'fade', // 默认动态路由过渡

curSelected: 1, // 当前选中的tab
direction: '', // 路由方向
curCount: 0 // 当前购物车中商品数
}
},
components: {Tabbar, TabbarItem},
beforeMount() {

},
mounted() {

vm = this
window.youniMall.userAuth = vm.$store.state.global.wxInfo
!vm.$store.state.global.wxInfo.dict ? vm.getDict() : null
},
computed: {
'showTabbar'() {
let path = this.$route.name
return path === 'home' || path === 'nearby' || path === 'ticket' || path === 'order' || path === 'user'
},
'key'() {
return this.$route.path.replace(/\//g, '_')
}
},
methods: {

getPageStatus(data) {
vm.curSelected = data
},
getDict() {
vm.loadData(commonApi.dict, {}, 'POST', function (res) {
vm.$store.commit('storeData', {key: 'dict', data: res.data.itemList})
}, function () {
})
}
},
watch: {
'curCount'() {
return vm.$store.state.cart.count
},
'$route'(to, from) {
/* let isBack = this.$router.isBack //  监听路由变化时的状态为前进还是后退
console.log(isBack)
if (isBack) {
vm.direction = 'reverse'
} else {
vm.direction = 'forward'
}
this.$router.isBack = false
const toDepth = to.path.split('/').length
const fromDepth = from.path.split('/').length
vm.direction = toDepth < fromDepth ? 'forward' : 'reverse' */

switch (to.name) {
case 'home':
vm.curSelected = 1
break
case 'nearby':
vm.curSelected = 2
break
case 'ticket':
vm.curSelected = 3
break
case 'order':
vm.curSelected = 4
break
case 'user':
vm.curSelected = 5
break
default:

break
}
}
}
}



