
/* eslint-disable */
let me
let vm
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {orderApi, userApi} from '../../service/main.js'
export default {
name: 'my-coupons',
data () {
return {
show: false,
curOrderFilter: '',
coupons: [],
params: {
type: 0,
pagerSize: 10,
pageNo: 1,
goodsType: 'XXX',
goodsCategory: '',
brandId: '',
filter: ''
},
onFetching: false,
isPosting: false,
}
},
components: {Tab, TabItem},
beforeMount() {
me = window.me
},
mounted () {
vm = this
vm.getCoupons()
vm.$nextTick(() => {
vm.$refs.couponScroller.finishInfinite(true)
vm.$refs.couponScroller.resize()
})
},
computed: {},
watch: {
'$route' (to, from) {
vm.getCoupons()
}
},
methods: {

setPageStatus (data) {
this.$emit('listenPage', data)
},
refresh (done) {
console.log('下拉加载')
setTimeout(function () {
vm.getCoupons()
vm.$refs.couponScroller.finishPullToRefresh()
}, 1200)
},
infinite (done) {
console.log('无限滚动')
setTimeout(function () {
vm.getCoupons(true)
vm.$refs.couponScroller.finishInfinite(true)
}, 1000)
},
onItemClick (type) {
if (type === 'undefined') {
vm.params.type = ''
} else {
vm.params.type = type
}
vm.getCoupons()
},
filterTicket (type, isMine) {
vm.curTicketFilter = type
vm.getCoupons()
},
getCoupons (isLoadMore) {
vm.params.type = vm.$route.params.type
if (vm.onFetching) return false
vm.processing()
vm.onFetching = true
vm.loadData(userApi.coupons, vm.params, 'POST', function (res) {
var resD = res.data.itemList
/* for (var i = 0; i < resD.length; i++) {
switch (resD[i].status) {
case -1:
resD[i].statusName = '已取消'
break
case 0:
resD[i].statusName = '待支付'
break
case 1:
resD[i].statusName = '待派送'
break
case 2:
resD[i].statusName = '派送中'
break
case 3:
resD[i].statusName = '待评价'
break
case 4:
resD[i].statusName = '已完成'
break
}
} */
if (!isLoadMore) {
vm.coupons = resD
} else {
vm.coupons.push(resD)
}
console.log(vm.coupons, '优惠券数据')
vm.onFetching = false
vm.processing(0, 1)
}, function () {
vm.onFetching = false
vm.processing(0, 1)
})
},
delOrder (id) {
vm.confirm('确认删除？', '订单删除后不可恢复！', function () {
vm.loadData(orderApi.delOrder + '?id=' + id, vm.params, 'POST', function (res) {
vm.isPosting = true
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {
})
},
cancelOrder (id) {
if (vm.isPosting) return false
vm.confirm('确认删除？', '订单删除后不可恢复！', function () {
vm.loadData(orderApi.delOrder, vm.params, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {

})
}
}
}



