
/* eslint-disable */
let me
let vm
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Search from 'vux/src/components/search/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import {orderApi, userApi} from '../../service/main.js'

export default {
name: 'my-coupons',
data() {
return {
show: false,
curOrderFilter: '',
coupons: [],
params: {
type: 0,
pagerSize: 10,
pageNo: 1,
couponNote: '',
discountAmount: '',
maxDiscountAmount: '',
up_amount: ''
},
noMore: false,
isPosting: false
}
},
components: {Tab, TabItem, Swipeout, SwipeoutItem, SwipeoutButton},
beforeMount() {
me = window.me
},
mounted() {
vm = this
vm.getCoupons()
vm.$nextTick(() => {
vm.$refs.couponScroller.finishInfinite(true)
vm.$refs.couponScroller.resize()
})
},
computed: {},
watch: {
'$route'(to, from) {
vm.getCoupons()
}
},
methods: {

setPageStatus(data) {
this.$emit('listenPage', data)
},
refresh(done) {
console.log('下拉加载')
setTimeout(function () {
vm.getCoupons()
vm.$refs.couponScroller.finishPullToRefresh()
}, 1200)
},
infinite(done) {
console.log('无限滚动')
setTimeout(function () {
vm.getCoupons(true)
vm.$refs.couponScroller.finishInfinite(true)
}, 1000)
},
onItemClick(type) {
type ? vm.params.type = type : delete vm.params.type
vm.getCoupons()
},
getCoupons(isLoadMore) {
if (vm.isPosting) return false
vm.processing()
vm.isPosting = true
vm.loadData(userApi.couponsList, vm.params, 'POST', function (res) {
vm.isPosting = false
vm.processing(0, 1)
var resD = res.data.pager
/*if(resD.itemList.length){
for (var i = 0; i < resD.itemList.length; i++) {
var cur=resD.itemList[i]
switch (cur.status) {
case -1:
cur.statusName = '已取消'
break
case 1:
cur.statusName = '待支付'
break
case 2:
cur.statusName = '待派送'
break
case 3:
cur.statusName = '派送中'
break
/!*case 3:
cur.statusName = '待评价'
break*!/
case 4:
cur.statusName = '已完成'
break
}
}
}*/
if (!isLoadMore) {
if (resD.totalCount < vm.params.pageSize) {
vm.noMore = true
}else{
vm.noMore = false
}
vm.coupons = resD.itemList
} else {
resD.itemList.length ? vm.coupons.concat(resD.itemList) : vm.noMore = true
}
console.log(vm.coupons, '优惠券数据')
}, function () {
vm.isPosting = false
vm.processing(0, 1)
})
},
del(id) {
vm.confirm('确认删除？', '删除后不可恢复！', function () {
vm.loadData(userApi.delCoupon, {id:id}, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {
})
},
onButtonClick(type, id) {
if (type === 'edit') {
this.$router.push({name: 'edit_coupon', query: {id: id}})
} else {
vm.del(id)
}
}
}
}



