
/* eslint-disable no-unused-vars */
let me
let vm
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import {userApi} from '../../service/main.js'

export default {
name: 's-topic',
data() {
return {
onFetching: false,
isPosting: false,
sellerId: null,
topic: ''
}
},
components: {Group,XTextarea},
beforeMount() {
me = window.me
},
mounted() {
vm = this


vm.sellerId = vm.$store.state.sellerId
console.log(vm.sellerId)
},
methods: {
validate() {
if (!vm.topic) {
vm.toast('请填写公告内容！')
return false
}
},
update() {
if (vm.isPosting || !vm.validate()) return false
vm.isPosting = true
vm.processing()
vm.loadData(userApi.updateSeller, {
id: vm.sellerId,
name: vm.storeName,
avtar: vm.storeImg,
authInfo: vm.authInfo,
description: vm.description,
topic: vm.topic,
area: vm.area,
detailAddress: vm.detailAddress
}, 'POST', function (res) {
this.$store.commit('updateStore', res.data)
vm.$router.back()
vm.isPosting = false
vm.processing(0, 1)
}, function () {
vm.isPosting = false
vm.processing(0, 1)
})
}
}
}



